// search menu blog
$(document).ready(function () {
    $('#searchImageblog').click(function () {
        $('#mob').hide();
        $('.navbar-toggle').hide();
        $('#shoppingBag').hide();
//                $('.searchImageblog').hide();
        $('#expandbox').show();
        $('#searchImageblog').css({'visibility': 'hidden'});
        $('#searchForm').css({'margin-top': '30px'});
        $('#cancel').show();
    });

    $('#cancel').click(function () {
        $('#mob').show();
        $('#shoppingBag').show();
        $('.navbar-toggle').show();
        $('#expandbox').hide();
        $('#searchImageblog').css({'visibility': 'visible'});
        $('#cancel').hide();
    });
//end search menu blog

    var internaWidth = $("#bs-example-navbar-collapse-1").outerWidth();
    var internaWidthUl = $("#widthMyTextMenu").outerWidth();
    var internaWidthMySearch = $("#MySearch").outerWidth();
    var UlAndMenu = internaWidthUl + internaWidthMySearch;
    var padding = internaWidth - UlAndMenu;

    if ($(window).width() < 768) {
        $('#globalHeader').css('visibility', 'hidden');
        $('.marginInSize').css('margin-top', '-54px');
//            $('#boxSearch').css('margin-bottom', '50px');
    }
    if ($(window).width() > 768) {


        $('#widthMyTextMenu').css('padding-right', padding / 2);
        $('ul li a').hover(function () {
            $('.dropdown-menu').css('padding-right', padding / 2);

        });
    }


    if ($(window).width() <= 768) {
        $('#navreview').addClass('navbar-fixed-top');
        $('#marginInMobile').css('visibility', 'visible');
    }
    if ($(window).width() > 768) {
        $('#marginInMobile').css('visibility', 'hidden');
        $('#marginInMobile').css('display', 'none');
    }
    $(window).scroll(function () {
        if ($(window).width() <= 768) {
            var d = $('#review-nav');
            if ($(document).scrollTop() <= 85) {
                $('#review-nav').addClass('fixedMenu');
                $('.review-base-menu img#mob').addClass('logoMobile');
                $('#searchImage').addClass('searchImage');
            }
            else {
                $('#review-nav').removeClass('fixedMenu');
                $('.review-base-menu img#mob').removeClass('logoMobile');
                $('#searchImage').removeClass('searchImage');
            }
        }
    });

    $(document).ready(function () {
        jQuery('body').bind('click', function (e) {
            if (jQuery(e.target).closest('.navbar').length == 0) {
                // click happened outside of .navbar, so hide
                var opened = jQuery('.navbar-collapse').hasClass('in');
                if (opened === true) {
                    jQuery('.navbar-collapse').collapse('hide');
                    //console.log('test1');
                    $('.menuReviewHoverSm').css({'display': 'none', 'visibility': 'hidden'});
                }
            }
        });
    });
    // for back gray by hover
    $('.myDelay').hover(function () {
        $('.menuReviewHoverSm').css({'display': 'block'});
    });

    $('.myDelay').mouseleave(function () {
        $('.menuReviewHoverSm').css({'display': 'none'});
    });

var isMenuInProcess = false;
    $('.myDelay1').click(function () {
        setTimeout(function() {
            if (!isMenuInProcess) {
                isMenuInProcess = true;

                console.log($(this).hasClass('collapsed'));
                console.log($('.myDelay1').hasClass('collapsed'));
                if ($('.myDelay1').hasClass('collapsed') === false) {

                    $('.menuReviewHoverSm').css({'display': 'block', 'visibility': 'visible'});
                }
                else {
                    $('.menuReviewHoverSm').css({'display': 'none', 'visibility': 'hidden'});
                }
                isMenuInProcess = false;
            }
        },50);
    });


    // time out menu
    $(document).ready(function () {
        if ($(window).width() > 767) {
            var hoverTimeout;
            $(".dropdown-toggle").hover(function () {
                var id = $(this).attr('id');
                var width = $(this).outerWidth();
                $(this).css({
                    'padding-right': '15px',
                    'padding-left': '15px', 'transition-delay': '0.3s'
                });
                var margin = parseInt(width) / 2;
                var menu = id.split("-"),
                    menuId = menu[1];
                $('.up-arrow' + menuId).css({
                    'margin-right': parseInt(margin) - 5,
                    'margin-left': parseInt(margin),
                    'visibility': 'visible',
                    'text-align': 'center',
                    'transition-delay': '0.3s',
                    'cursor': 'pointer',
                    'content': '',
                    'display': 'block',
                    'position': 'absolute',
                    'top': '75%',
                    'width': '0',
                    'height': '0',
                    'border': '5px solid transparent',
                    'border-bottom-color': '#febe1b'
                });
                $(this).css({
                    'transition-delay': '0.3s',
                    'border-bottom': '1px solid #FEBE1B',
                    'color': 'rgb(255,186,37) !important;'
                });
                var element = $(this).parent(".dropdown");
                hoverTimeout = setTimeout(function () {
                    element.addClass("open");
                    $('.menuReviewHoverSm').css({'visibility': 'visible'})

                }, 300);
            }, function () {
                clearTimeout(hoverTimeout);
                var element = $(this).parent(".dropdown");
                var id = $(this).attr('id');
                var menu = id.split("-"),
                    menuId = menu[1];
                console.log('nuhover');
                $('.up-arrow' + menuId).css({
                    'transition-delay': '0.0001s',
                    'visibility': 'hidden',
                    'content': '',
                    'display': 'block',
                    'position': 'absolute',
                    'top': '75%',
                    'width': '0',
                    'height': '0',
                    'border': '5px solid transparent',
                    'border-bottom-color': '#febe1b'
                });
                $(this).css({
                    'transition-delay': '0.0001s', 'border-bottom': 'none'
                });
                hoverTimeout = setTimeout(function () {
                    element.removeClass("open");
                    $('.menuReviewHoverSm').css({'visibility': 'hidden'})
                }, 100);

                $(".dropdown-menu").hover(function () {
                    clearTimeout(hoverTimeout);
                }, function () {
                    setTimeout(function () {
                        element.removeClass("open");
                    }, 100);
                });
            });
        }
    });
    $('.submenu').hover(function (e) {
        var id = $(this).attr('id');
        var menu = id.split("-"),
            menuId = menu[1];
        $('.up-arrow' + menuId).css({
            'cursor': 'pointer',
            'visibility': 'visible',
            'text-align': 'center',
            'content': '',
            'display': 'block',
            'position': 'absolute',
            'top': '75%',
            'width': '0',
            'height': '0',
            'border': '5px solid transparent',
            'border-bottom-color': '#febe1b'
        });
        $('#tagAInAMenu-' + menuId).css({
            'border-bottom': '1px solid #FEBE1B'
        });
    });

    $('.submenu').mouseleave(function (e) {
        var id = $(this).attr('id');
//                console.log(id);
        var menu = id.split("-"),
            menuId = menu[1];
        $('.up-arrow' + menuId).css({
            'visibility': 'hidden',
            'content': '',
            'display': 'block',
            'position': 'absolute',
            'top': '75%',
            'width': '0',
            'height': '0',
            'border': '5px solid transparent',
            'border-bottom-color': '#febe1b'
        });
        $('#tagAInAMenu-' + menuId).css({
            'border-bottom': 'none'
        });
    });
});

// end menu


