// like

$(".reviewcommentId").unbind("click").click(function () {
    var reviewCommentId = $(this).attr('id');
    console.log(reviewCommentId);
    $.ajax({
        type: 'post',
        url: '/likeComment',
        data: {commentId: reviewCommentId, _token: $('#token').val()},
        success: function (res) {
            console.log(res['like']);
            console.log(res['dislike']);
            console.log('like shod');
            var count = $("#" + reviewCommentId + ".counterLike");
            if (count.attr('id') === reviewCommentId) {
                count.html(res['like']);
                count.val(res['like']);
            }

            var countDis = $("#" + reviewCommentId + ".counterdisLike");
//                    console.log(countDis.attr('id') === reviewdisLike);
            if (countDis.attr('id') === reviewCommentId) {
                countDis.html(res['dislike']);
                countDis.val(res['dislike']);
            }
        }
    });

});

