// storecomment

$('.sendCommentreview').on('click', function () {
    var articleId = $(this).attr('id');
    $.ajax({
        type: 'post',
        url: '/storecomment',
        data: {articleId: articleId, comment: $('.comment').val(), _token: $('#token').val()},
        success: function (res) {
            $('.comment').val("");
            console.log(res);
            if (res === 'کاربر گرامی نظر شما به ثبت رسیده است.بعد از تایید مدیر در سایت به نمایش در می آید.') {
                swal({
                    title: res,
                    type: "warning",
                    timer: 3000,
                    showCancelButton: false,
                    showConfirmButton: false
                })
            }
            else {
                swal({
                    title: res,
                    type: "success",
                    timer: 3000,
                    showCancelButton: false,
                    showConfirmButton: false
                })
            }

        },
        error: function (res) {
//                    console.log(JSON.parse(res.responseText)['comment']);
            swal({
                title: JSON.parse(res.responseText)['comment'],
                showCancelButton: false,
                showConfirmButton: false,
                type: "error",
                timer: 2000
            });
        }

    });
});

$('.sendCommentreply').on('click', function () {
    var commentId = $(this).attr('id');
    var articleId = $('#articleId').val();
    $.ajax({
        type: 'post',
        url: '/storecommentreply',
        data: {
            commentId: commentId,
            articleId: articleId,
            comment: $("#" + commentId + ".ReplyComment").val(),
            _token: $('#token').val()
        },
        success: function (res) {
            $("#" + commentId + ".ReplyComment").val("");
            swal({
                title: res,
                type: "success",
                timer: 3000,
                showCancelButton: false,
                showConfirmButton: false

            })
        },
        error: function (res) {
//                    console.log(JSON.parse(res.responseText)['comment']);
            swal({
                title: JSON.parse(res.responseText)['comment'],
                showCancelButton: false,
                showConfirmButton: false,
                type: "error",
                timer: 2000
            });
        }

    });
});


$('.ReplyComment').on('keydown', function () {
    var commentId = $(this).attr('id');
    var ReplyComment = $("#" + commentId + ".ReplyComment");
    var ReplyCounter = $("#" + commentId + ".ReplyCounter");
    ReplyComment.keyup(updateCount);
    ReplyComment.keydown(updateCount);
    function updateCount() {
        var cs = ReplyComment.val().length;
        ReplyCounter.text(cs);
        ReplyCounter.val(cs);
    }
});

$('.comment').on('keydown', function () {
    var comment = $('.comment');
    var counter = $('.counter');
    comment.keyup(updateCount);
    comment.keydown(updateCount);
    function updateCount() {
        var cs = comment.val().length;
        counter.val(cs);
        if (cs > 0) {
            $('#logincomment').removeAttr("disabled");
            $('#logincomment').css('cursor', 'pointer');
        }
        else {
            $("#logincomment").attr("disabled", "disabled");
            $('#logincomment').css('cursor', 'no-dorp');
        }
    }
});


